import { marksmanshipPointsMap } from '../consts/marksmanshipPointsMap';

export function doMarksmanshipDataMath({ marksmanship, hitCountData }) {
    return marksmanship.map(({ head, targetName, torso }) => {
      const { headTotal, torsoTotal } = hitCountData.find(({ targetName: tn }) => targetName = tn)
      const { a: headA, c: headC } = head
      const { a: torsoA, c: torsoC, d: torsoD } = torso
      const headMiss = headTotal - (headA + headC)
      const torsoMiss = torsoTotal - (torsoA + torsoC + torsoD)
      return ({
        targetName,
        head: {
          ...head,
          m: headMiss
        },
        torso: {
          ...torso,
          m: torsoMiss
        },
        points:
          ((headA + torsoA) * marksmanshipPointsMap.a) +
          ((headC + torsoC) * marksmanshipPointsMap.c) +
          ((torsoD) * marksmanshipPointsMap.d) +
          ((headMiss + torsoMiss) * marksmanshipPointsMap.d)
      })
    })
  }

  export function doCOFMath({ cof, hitCountData }) {
    const { marksmanship, time } = cof
    const totalTime = Number(Math.round(time.reduce((acc, it) => acc + it)).toFixed(2))
    const marksmanshipWithMathDone = doMarksmanshipDataMath({ marksmanship, hitCountData })
    const totalMarksmanshipPoints = marksmanshipWithMathDone.map(({ points }) => points).reduce((acc, it) => acc + it)
    const hitFactor = Number((totalMarksmanshipPoints / totalTime).toFixed(3)) || 0
    return {
        marksmanship:marksmanshipWithMathDone,
        time,
        totalTime,
        hitFactor,
        totalMarksmanshipPoints
    }
  }