import { Link, useParams } from 'react-router-dom'
import { getHistoryDetailAtIndex } from '../storage/getHistoryCOF';
import { LinkWrapper } from '../components/Links';
import { doMarksmanshipDataMath } from '../helpers/math';



export default function TotalScore({ getPageData }) {
  const { cofId, runIndex } = useParams()
  const pageData = getPageData(cofId)
  const { hitCountData } = pageData
  const historyDetail = getHistoryDetailAtIndex({ cofId, runIndex })
  const { marksmanship: marksmanshipFromStorage, time } = historyDetail
  const marksmanshipWithMathDone = doMarksmanshipDataMath({ marksmanship: marksmanshipFromStorage, hitCountData })
  const totalMarksmanshipPoints = marksmanshipWithMathDone.map(({ points }) => points).reduce((acc, it) => acc + it)
  const totalTime = Math.round(time.reduce((acc, it) => acc + it)).toFixed(2)
  const hitFactor = (totalMarksmanshipPoints / totalTime).toFixed(3)

  const scoreStyle = { fontSize: 25, textAlign: 'right', display: 'block' }
  return (
    <main style={{ padding: "1rem 0" }}>
      <h2 style={{ color: '#CEFF51' }}>SCORING</h2>
      <section>
        <h3>Hit Factor score <span style={{ ...scoreStyle, fontSize: 30 }}>{hitFactor}</span></h3>
        <h4>Total marksmanship points <span style={scoreStyle}>{totalMarksmanshipPoints}</span></h4>
        <h4>Total time <span style={scoreStyle}>{totalTime}</span> </h4>
      </section>
      <LinkWrapper>
        <Link to={`/history/${cofId}`}>View History</Link>
      </LinkWrapper>
      <LinkWrapper>
        <Link to={`/`}>Home</Link>
      </LinkWrapper>
    </main>
  );
}